import { useEffect, useRef, useState } from "react";
import { useSpring, animated, config } from "@react-spring/web";
import { useDrag } from "react-use-gesture";

import Calendar from "./Components/Calendar";
import Agenda from "./Components/Agenda";

import { CgMenuGridO } from "react-icons/cg";
import "./styles/index.scss";

function App() {
	return (
		<>
			<header>
				<h2>Batch '23</h2>
				<div className="menu">
					<CgMenuGridO />
				</div>
			</header>
			<Main />
		</>
	);
}
const Main = () => {
	const [minCal, setMinCal] = useState(false);
	const [selectedDate, setSelectedDate] = useState(new Date());

	// Springy Stuff

	const [{ marginTop }, set] = useSpring(() => ({
		marginTop: "-5rem",
		config: config.slow,
	}));

	const bind = useDrag(
		({ last, vxvy: [, vy], movement: [, my], cancel, canceled, tap }) => {
			if (tap) return;
			const cur = Number(marginTop.get().slice(0, -3));
			console.log(last, my, vy, cur);

			if (-5 < cur || cur < -21 || my > 90 || my < -90) {
				console.log("Cancel");
				cancel();
			}

			if (last) {
				console.log("Last");
				if (cur < -8 || vy < -0.1)
					set({
						marginTop: "-20rem",
						immediate: false,
						config: canceled ? config.wobbly : config.slow,
					});
				if (cur >= -8 || vy > 0.1)
					set({
						marginTop: "-5rem",
						immediate: false,
						config: canceled ? config.wobbly : config.slow,
					});
			} else {
				console.log("Hehe");
				set({
					marginTop: `${Number(marginTop.get().slice(0, -3)) + my / 18}rem`,
					immediate: true,
					config: config.slow,
				});
			}
		},
		{
			bounds: { marginTop: "-20rem" },
			delay: 2000,
			rubberband: true,
			filterTaps: true,
		}
	);

	return (
		<>
			<div className="calendar-container">
				<Calendar
					minimised={minCal}
					selectedDate={selectedDate}
					setSelectedDate={setSelectedDate}
				/>
			</div>
			<animated.div id="agenda" style={{ marginTop }}>
				<div className="resize" {...bind()}>
					<div />
				</div>
				<Agenda />
			</animated.div>
		</>
	);
};

export default App;
